
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
components: {
Cell,
CellBox,
Group,
XSwitch,
},
created(){
this.barinfo = this.$store.state.currentBar
this.getPay()
},
methods: {
getPay () {
this.$api.getBarConfigs(this.barinfo.bar_id,'bar_pay').then((data) => {
this.status = data[0].status
this.id = data[0].id
})
},
onChange (newVal) {
this.payUpdate()
},
payUpdate () {
let data = {
'bar_id': this.barinfo.bar_id,
'status': this.status,
'cfg_modules_name': 'bar_pay',
'cfg_type': 1
}
this.$api.updateModule(this.id, data).then((res) => {

})
}
},
data () {
return {
barinfo: {},
status: 2,
id: ''
}
}
}



