
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'


export default {
components: {
Cell,
Group,
CellFormPreview
},
data () {
return {
list: []
}
},
created () {
this.marketerList()
},
methods: {

marketerList () {
this.$api.getMarketerList(this.$barId).then((data) => {
this.list = []
for (let i = 0; i < data.length; i++) {
let obj1 = {
label: '手机号',
value: data[i].mobile
}
let obj2 = {
label: '夜吧号',
value: data[i].user_id
}
let objArr = [obj1, obj2]
Object.assign(objArr,data[i])
this.list.push(objArr)
}
})
},

marketerDelete (id) {
this.$api.deleteMarketer(id).then((res) => {
this.$util.remove(this.list,id)
})
}
},

}



