export default {
  name: 'LoadImg',
  data () {
    return {}
  },
  props: {
    url: {
      type: String
    },
    urls: {
      type: Array
    },
    borderRadius:{
      type:String
    }
  },
  methods:{
    onImgClick(){
      if (this.urls&&this.urls.length>0){
        window.wx.previewImage({
          current:this.url,
          urls:this.urls
        })
      }
      else {
        window.wx.previewImage({
          current:this.url,
          urls:[this.url]
        })
      }
    }
  }
}



