
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import MyFoot from '../../components/MyFoot'

export default {
directives: {
TransferDom
},
components: {
Cell,
CellBox,
Group,
Loading,
XSwitch,
TransferDom,
Popup,
XInput,
MyFoot
},
data () {
return {
barinfo: {},
list: [],
cfg_value: {}
}
},
created () {
this.barinfo = this.$util.getStorage(this.$config.STORAGE.BAR_INFO)
this.getModulesList()
},
methods: {

getModulesList () {
this.$api.getModule(this.barinfo.barid).then((res) => {
this.list = res.data.data
console.log(res)
})
},

modulesUpdate (e) {
let that = this
if (e) {
for (var i = 0; i < this.list.length; i++) {
let data = {
'bar_id': this.barinfo.barid,
'status': this.list[i].status,
'cfg_modules_name': this.list[i].cfg_modules_name,
'cfg_value': this.list[i].cfg_value
}
this.$api.updateModule(this.list[i].id, data).then((res) => {
console.log(res)
})
}
} else {
that.$router.go(-1)
}
}
},
filters: {
capitalize: function (value) {
switch (value) {
case 'redpacket_rank':
return '红包排行榜'
break
case 'popularity_rank':
return '霸屏排行榜'
break
case 'gift_rank':
return '礼物排行榜'
break
case 'occupyscreen_rank':
return '人气排行榜'
break
case 'go_wall':
return '上墙排行榜'
break
}
}
},

}



