
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'


export default {
components: {
Cell,
Group,
CellFormPreview
},
data () {
return {
list: [],
}
},
created () {
this.barTableList()
},
methods: {

barTableList () {
this.$api.getBarTableList(this.$barId).then((data) => {
for (var i = 0; i < data.length; i++) {
let obj = data[i]
let obj1 = {
label: '消费者',
value: obj.user_name + '(' + obj.user_mobile + ')'
}
let obj2 = {
label: '需付金额',
value: obj.menu_price
}
let obj3 = {
label: '到场时间',
value: obj.arrive_at
}
let obj4 = {
label: '营销人员',
value: obj.marketer_name
}
let obj5 = {
label: '套餐名称',
value: obj.menu_name
}
let objArr
if (status == 1) {
objArr = [obj5, obj2, obj3]
} else if (status == 2) {
objArr = [obj1, obj2, obj3, obj4, obj5]
} else if (status == 3) {
objArr = [obj5, obj2, obj3, obj4]
} else {
objArr = [obj5, obj2, obj3, obj4]
}
Object.assign(objArr,obj)
this.list.push(objArr)
}
})
},

consume (id) {
let data = {
'order_id': id,
'bar_id': this.barinfo.bar_id
}
this.$api.orderConsume(data).then((res) => {

})
}
},
filters: {
capitalize: function (value) {
switch (value) {
case 1:
return '待付款'
break
case 2:
return '已付款'
break
case 3:
return '已消费'
break
case 4:
return '已退单'
break
}
}
}
}



